
.plugin {
  background-color: #4682b4;
}
.theme {
  background-color: #46B56B;
}


.stuff-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
}

.stuff.card {
  cursor: pointer;
  display: inline-block;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin: 5px;
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: calc(25% - 10px);
      flex-basis: calc(25% - 10px);
  min-width: 311px;
  max-width: 391px;
  -ms-flex-item-align: center;
      align-self: center;
  border-radius: 5px;
}

.stuff.card header {
  text-align: center;
  min-height: 5.6rem;
  position: relative;
  border-radius: 5px 5px 0 0;
}

.stuff.card header h3 {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  z-index: 1;
  padding: 0 5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.stuff.card main {
  margin: 0 5%;
}

.stuff.card .tag {
  text-transform: uppercase;
  font-style: italic;
  font-weight: 100;
}

.stuff.card .tag + .tag {
  padding-left: 1ch;
}

.stuff.card .tag::before {
  content: "#";
  position: relative;
  top: -.05em;
}

.stuff.card p::after {
  float: right;
  font-weight: bolder;
  -webkit-transition: opacity .3s;
  -o-transition: opacity .3s;
  transition: opacity .3s;
  opacity: .2;
}
.stuff.card:hover p::after {
  opacity: 1;
}
.stuff.card.plugin p::after {
  content: "PLUGIN"
}
.stuff.card.theme p::after {
  content: "THEME"
}

.card.obsolete, .card.broken, .card.dead {
  opacity: 1;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.card.obsolete:not(:hover),
.card.dead:not(:hover) {
  opacity: .5;
  -webkit-filter: grayscale(.85);
          filter: grayscale(.85);
}

.card.broken:not(:hover) {
  opacity: .75;
  -webkit-filter: grayscale(.5);
          filter: grayscale(.5);
}

.stuff.modal footer h3 {
  display: inline-block;
  width: 50%;
  text-align: center;
}


