  width: 50%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: .5em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.modal .name-value-pair .name,
.modal .name-value-pair .value {
  -ms-flex: auto;
      flex: auto;
}

.modal .name-value-pair .name {
  font-weight: 300;
}

.modal .name-value-pair .value {
  text-align: right;
  padding: 0;
