.changelog-table {
  display: grid;
  max-height: 40vh;
  overflow-y: auto;
  grid-auto-columns: auto 100fr;
  grid-column-gap: .7ch;
  grid-row-gap: calc(3em / 14);
  padding-bottom: calc(3em / 7);
  margin-right: -1ch;
}

.changelog .version {
  grid-column: 1;
  font-variant-numeric: tabular-nums; /* doesn't seem to do anything; guess the font is broken */
  text-align: center;
  padding-left: .5ch;
}

.changelog .version::after {
  content: ":";
}

.changelog .message {
  white-space: pre-wrap;
  grid-column: 2;
  font-weight: 300;
  text-indent: -.8ch;
  padding-left: .8ch;
}


.dependency + .download {
  margin-left: .5ch;
