
.backdrop {
  position: relative;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  opacity: 0;
  -webkit-animation: fade-to-60 .25s forwards;
          animation: fade-to-60 .25s forwards;
}

.modals-wrapper {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  overflow: hidden;
}

@-webkit-keyframes fade-to-60 {
  to {
    opacity: .6;
  }
}

@keyframes fade-to-60 {
  to {
    opacity: .6;
  }
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1);
      -ms-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  background: #e8e8e8;
  width: 50vw;
  min-width: 500px;
  /* min-height: 200px; */
  max-height: 100vh;
  -webkit-transition: left .25s, top .25s, opacity .25s, -webkit-transform .25s;
  transition: left .25s, top .25s, opacity .25s, -webkit-transform .25s;
  -o-transition: left .25s, top .25s, transform .25s, opacity .25s;
  transition: left .25s, top .25s, transform .25s, opacity .25s;
  transition: left .25s, top .25s, transform .25s, opacity .25s, -webkit-transform .25s;
  opacity: 1;
  /* border: 1px solid rgba(0,0,0,.7); */
  border-radius: 5px;
  color: white;
}

.modal header,
.modal footer {
  padding: 0 5%;
}
.modal header {
  border-radius: 5px 5px 0 0;
}
.modal main {
  padding: 30px;
  padding-bottom: 40px;
}
.modal footer {
  border-radius: 0 0 5px 5px;
}

.modal header h2 {
  position: relative;
  text-align: center;
}

.modal a {
  /* transition: opacity .3s; */
  opacity: .38;
  color: white;
}

.modal a:hover {
  opacity: 1;
}



