
#snowfield {
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 100000;
  position: fixed;
  left: 0; top: 0;
}

.snowflake {
  position: absolute;
  color: #fff;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}

.snowflake-solid {
  border-radius: 50%;
  background: #fff;
}



