
.search {
  position: absolute;
  top: -3.6rem;
  right: 0;
  margin-right: calc(10px + 2%);
  display: inline-block;
  line-height: 2;
  font-size: 1.8rem;
  vertical-align: middle;
}

.search input {
  border: none;
  padding: 10px;
  display: inline-block;
  position: relative;
  top: -.25rem;
  margin:.5ch;
  color: white;
  background-color: rgba(0,0,0, .2);
  width: 500px;
  border-radius: 5px;
  max-width: calc(90vw - 8rem);
}

.search input[type=date] {
  padding: 0;
  padding-top: 1px;
}



