
#root > header h1 {
  display: inline-block;
}

.header-links {
  display: inline-block;
  margin-left: 3ch;
  margin-right: -5%;
  height: 4.4rem;
  padding-top: 1.3rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.header-links .external {
  padding: 0 1ch 1px 1ch;
  margin: .5ch;
  background: #e8e8e8;
  border-radius: 1ch;
  color: #024;
  line-height: 2;
}

.header-links .external::before {
  content: "";
  display:inline-block;
  width: 1em;
  height: 1em;
  position: relative;
  left: -.25ch;
  top: 2px;
  background: 50% center / contain no-repeat;
}

.header-links .github::before {
  background-image: url("./icons/Github.svg");
}

.header-links .paypal::before {
  background-image: url("./icons/Paypal.svg");
}



