
@import url(font/palanquin.css);

:root {
  font-family: palanquin;
  font-weight: 500;
  /* font-size: 15pt; */
  font-size: 14pt;
  line-height: 1.1;
}

html { background: #27292B; }
body {
  margin: 0;
  padding: 0;
  background: rgba(0,0,0, .2);
}
html, body {
  width: 100vw;
  height: 100vh;
}

#root > header,
#root > main,
#root > footer {
  padding: 0 5%;
  color: white;
}

#root > header * {
  vertical-align: middle;
}

#root > header {
  /* display: flex; */
  /* shifts text in first row of stuff cards for whatever reason  */
}

h1, h2, h3, h4
/* , h5, h6 */
{
  margin: 0;
  font-weight: 600;
  line-height: 2;
  position: relative;
}

/*
h6 {
  font-size: 0.8rem;
}
h5 {
  font-size: 1.0rem;
} */

h4 {
  font-size: 1.2rem;
}
h3 {
  font-size: 1.4rem;
}
h2 {
  font-size: 1.8rem;
}
h1 {
  font-size: 2.2rem;
}

p {
  margin: 1rem 0;
}

a {
  color: #ccc;
  text-decoration: inherit;
  position: relative;
}

#root > main .external {
  -webkit-transition: opacity .3s;
  -o-transition: opacity .3s;
  transition: opacity .3s;
  opacity: .38;
  color: white;
}

#root > main .external:hover {
  opacity: 1;
}

.title a:hover::before {
  content: "#";
  position: absolute;
  left: -1.5rem;
  line-height: 1.9;
  -webkit-animation: fade-in .25s;
          animation: fade-in .25s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}




header, footer {
  background: rgba(0,0,0, .21);
}

header {
  border-bottom-width: .1rem;
}

footer {
  border-top-width: .1rem;
}



#root {
  max-height: 100vh;
  overflow-y: auto;
  background-color: #27292B;
}


/* ::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,.2);
}

::-webkit-scrollbar-button,
::-webkit-scrollbar-corner,
::-webkit-scrollbar-resizer {
  display: none;
  max-width: 0;
  max-height: 0;
} */

* {
  scroll-behavior: smooth;
}



